$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

.page-template-page-about {
  .aboutIntroInner {
    text-align: center;
  }
  #nonProfit {
    background-color: $blue;
    .nonProfitInner {
      padding-top: 6rem;
      padding-bottom: 6rem;
      color: #fff;
    }
  }
  #ulpCommunity {
    .commMemberContainer {
      margin-bottom: 2rem;
      .commMemberImgWrap {
        width: 150px;
        height: 150px;
        border-radius: 50%;
        border: 2px solid $yellow;
        display: inline-block;
        margin: auto auto 1rem auto;
        img {
          border: 2px solid $white;
          border-radius: 50%;
          overflow: hidden;
        }
        &:hover {
          border-color: $blue;
        }
      }
    }
  }
}